/* src/components/ImageStack.css */
.responsive-image-stack {
    position: relative;
    width: 100%;
    height: auto; /* 自适应高度 */
  }
  
  .image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0;
    animation: fadeIn 0.5s forwards;
  }
  
  .top-image {
    z-index: 1;
  }
  
  @keyframes fadeIn {
    to {
      opacity: 1;
    }
  }
  
  /* 移动设备媒体查询 */
  @media (max-width: 600px) {
    .responsive-image-stack {
      height: 100vh; /* 使用视口高度确保全屏显示 */
    }
  
    .image {
      height: 100%; /* 确保图片高度自适应 */
    }
  }

  /* 确保在PC端有足够的高度 */
@media (min-width: 601px) {
    .responsive-image-stack {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 430px;
        height: 932px;
        overflow: hidden;
        border-radius: 20px;
    }
  }
  